import React, { Component } from 'react';
import "./header.css";

import Title from "./title/title.jsx";
import Navigation from "./navigation/navigation.jsx";

class Header extends Component {
    render() {
        return (
            <header>
                <Title />
                <Navigation />
            </header>
        );
    }
}

export default Header;


