import React, { Component } from "react";
import "./filter.css";
import Selection from "./selection.jsx";
import Slider from "./slider.jsx";

class Filter extends Component {
    constructor(props) {
        super(props)
        this.state = {
            visible: true
        }

        this.toggleVisibility = this.toggleVisibility.bind(this)
    }

    componentDidMount() {
        fetch(this.props.url)
            .then(res => res.json())
            .then(data => this.props.setGenres(data.genres))
            .catch(error => console.log(error));
    }

    toggleVisibility() {
        this.setState({ visible: !this.state.visible })
    }

    render() {
        const { genre, genres, onGenreChange, onChange, year, rating, runtime } = this.props;

        const filters = this.state.visible 
        ?   <div className="filters">                
                <Slider data={year} onChange={onChange} />
                <Slider data={rating} onChange={onChange} />
                <Slider data={runtime} onChange={onChange} />

                <Selection 
                genres={genres}
                genre={genre}
                onGenreChange={onGenreChange}
                />
            </div>
        : null

        return(
            <div className="filter">
                <button onClick={this.props.onSearchButtonClick}>
                    <i className="fas fa-search"></i> Search
                </button>
                <p onClick={this.toggleVisibility}>
                    <i className="fas fa-filter"></i> {this.state.visible ? "Hide" : "Show"} Filters
                </p>
                {filters}
            </div>
        );
    }
}

export default Filter;


