import React, { Component } from 'react';
import "./navigation.css";

import { Link } from "react-router-dom";

class Navigation extends Component {
    constructor(props) {
        super(props)

        this.state = {
            hamburgerMenu: false,
            visible: false
        }

        this.handleResize = this.handleResize.bind(this)
        this.toggleMenu = this.toggleMenu.bind(this)
    }

    handleResize() {
        if(window.innerWidth < 1000) {
            this.setState({ hamburgerMenu: true, visible: false})
        }
        else {
            this.setState({ hamburgerMenu: false, visible: false })
        }
    }

    toggleMenu(e) {
        this.setState({ visible: !this.state.visible })
        e.stopPropagation()
    }

    componentDidMount() {
        this.handleResize()
        window.addEventListener("resize", this.handleResize)
    }

    render() {
        const visibility = this.state.visible ? "show" : "hide"
        const menuClasses = `${visibility} menu`
        const coverClasses = `${visibility} menuCover`

        const menu = this.state.hamburgerMenu 
        ?   <div className="hamburger">
                <button className="menuButton" onClick={this.toggleMenu}>
                    <i className="fas fa-bars"></i>
                </button>
                <div className={menuClasses}>
                    <Link className={window.location.pathname === process.env.PUBLIC_URL + '/' ? "active" : null} to={process.env.PUBLIC_URL + '/'} onClick={this.toggleMenu}><i className="fas fa-home fa-fw"></i> Home</Link>
                    <Link className={window.location.pathname === process.env.PUBLIC_URL + '/movies' ? "active" : null} to={process.env.PUBLIC_URL + '/movies'} onClick={this.toggleMenu}><i className="fas fa-film fa-fw"></i> Movies</Link>
                    <Link className={window.location.pathname === process.env.PUBLIC_URL + '/settings' ? "active" : null} to={process.env.PUBLIC_URL + '/settings'} onClick={this.toggleMenu}><i className="fas fa-cog fa-fw"></i> Settings</Link>
                </div>
                <div className={coverClasses} onClick={this.toggleMenu} />
            </div>
        :   <ul>
                <li><Link className={window.location.pathname === process.env.PUBLIC_URL + '/' ? "active" : null} to={process.env.PUBLIC_URL + '/'}><i className="fas fa-home"></i> Home</Link></li>
                <li><Link className={window.location.pathname === process.env.PUBLIC_URL + '/movies' ? "active" : null} to={process.env.PUBLIC_URL + '/movies'}><i className="fas fa-film"></i> Movies</Link></li>
                <li><Link className={window.location.pathname === process.env.PUBLIC_URL + '/settings' ? "active" : null} to={process.env.PUBLIC_URL + '/settings'}><i className="fas fa-cog"></i> Settings</Link></li>
            </ul> 

        return (
            <div className="navigation">
                {menu}
            </div>
        );
    }
}

export default Navigation;


