import React, { Component } from "react";
import "./main.css";

import Filter from "./filter/filter.jsx";
import Movies from "./movies/movies.jsx";

class Main extends Component {
    constructor(props) {
        super(props)

        this.state = {
            genre: "Comedy",
            genres: [],
            page: 1,
            total_pages: 1,
            movies: [],
            url: `https://api.themoviedb.org/3/genre/movie/list?api_key=a196ed572e4a41f030b932942e47e161&language=en-US`,
            moviesUrl: `https://api.themoviedb.org/3/discover/movie?api_key=a196ed572e4a41f030b932942e47e161&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=false&page=1`,
            year: {
                label: "year",
                min: 1990,
                max: 2017,
                step: 1,
                value: { min: 2000, max: 2017 }
            },
            rating: {
                label: "rating",
                min: 0,
                max: 10,
                step: 1,
                value: { min: 8, max: 10 }
            },
            runtime: {
                label: "runtime",
                min: 0,
                max: 300,
                step: 15,
                value: { min: 60, max: 120 }
            }
        }

        this.onGenreChange = this.onGenreChange.bind(this)
        this.onChange = this.onChange.bind(this)
        this.setGenres = this.setGenres.bind(this)
        this.generateUrl = this.generateUrl.bind(this)
        this.onSearchButtonClick = this.onSearchButtonClick.bind(this)
        this.fetchMovies = this.fetchMovies.bind(this)
        this.onPageIncrease = this.onPageIncrease.bind(this)
        this.onPageDecrease = this.onPageDecrease.bind(this)

        this.saveStateToLocalStorage = this.saveStateToLocalStorage.bind(this)
        this.getStateFromLocalStorage = this.getStateFromLocalStorage.bind(this)
    }

    saveStateToLocalStorage() {
        localStorage.setItem("wickedMovies.params", JSON.stringify(this.state))
    }

    getStateFromLocalStorage() {
        return JSON.parse(localStorage.getItem("wickedMovies.params"))
    }

    componentDidMount() {
        const savedState = this.getStateFromLocalStorage();
        if (!savedState || (savedState && !savedState.movies.length)) {
            this.fetchMovies(this.state.moviesUrl)
        } 
        else {
            this.setState({ ...savedState }, this.generateUrl(savedState))
        }
    }

    componentWillUpdate(nextProps, nextState) {
        this.saveStateToLocalStorage()

        if(this.state.moviesUrl !== nextState.moviesUrl) {
            this.fetchMovies(nextState.moviesUrl)
        }

        if(this.state.page !== nextState.page) {
            this.generateUrl(this.state)
        }
    }

    onPageDecrease() {
        const page = this.state.page;
        const nextPage = page - 1;

        if( nextPage > 0 ) {
            this.setState({ page: nextPage })
        }
    }

    onPageIncrease() {
        const { page, total_pages } = this.state;
        const nextPage = page + 1;

        if(nextPage <= total_pages) {
            this.setState({ page: nextPage })
        }
    }

    setGenres(genres) {
        this.setState({genres})
    }

    onGenreChange(event) {
        this.setState({ genre: event.target.value })
    }

    onChange(data) {
        this.setState({
            [data.type]: {
                ...this.state[data.type],
                value: data.value
            }
        })
    }

    onSearchButtonClick() {
        this.setState({page: 1}, this.generateUrl(this.state))
    }

    generateUrl(params) {
        const { genres, year, rating, runtime, page } = params;
        const selectedGenre = genres.find( genre => genre.name === params.genre );
        const genreId = selectedGenre.id;

        const moviesUrl = `https://api.themoviedb.org/3/discover/movie?` +
                        `api_key=a196ed572e4a41f030b932942e47e161&` +
                        `language=en-US&sort_by=popularity.desc&` +
                        `with_genres=${genreId}&` +
                        `primary_release_date.gte=${year.value.min}-01-01&` +
                        `primary_release_date.lte=${year.value.max}-12-31&` +
                        `vote_average.gte=${rating.value.min}&` +
                        `vote_average.lte=${rating.value.max}&` +
                        `with_runtime.gte=${runtime.value.min}&` +
                        `with_runtime.lte=${runtime.value.max}&` +
                        `page=${page}&`;

        this.setState({ moviesUrl })
    }

    fetchMovies(url) {
        fetch(url)
            .then(res => res.json())
            .then(data => this.storeMovies(data))
            .catch(error => console.log(error))
    }

    storeMovies(data) {
        const movies = data.results.map( result => {
            const { vote_count, id, genre_ids, poster_path, title, vote_average, release_date } = result;
            return { vote_count, id, genre_ids, poster_path, title, vote_average, release_date };
        });

        this.setState({ movies, total_pages: data.total_pages })
    }

    render() {
        return (
            <div className="main">
                <Filter 
                    onChange={this.onChange}
                    onGenreChange={this.onGenreChange}
                    setGenres={this.setGenres}
                    onSearchButtonClick={this.onSearchButtonClick}
                    {...this.state}
                />
                <Movies 
                    movies={this.state.movies}
                    page={this.state.page}
                    fetchedUrl={this.state.fetchedUrl}
                    onPageIncrease={this.onPageIncrease}
                    onPageDecrease={this.onPageDecrease}
                />
                <div className="rotatedSquare firstSquare" />
            </div>
        );
    }
}

export default Main;


