import React, { Component } from 'react';
import "./title.css";
import logo from "../../../images/logo.svg";

import { Link } from "react-router-dom";

class Title extends Component {
    render() {
        return (
            <div className="title">
                <Link to="/"><img src={logo} alt="Logo" /></Link>
            </div>
        );
    }
}

export default Title;


