import React, { Component } from "react";
import "./movies.css";
import MovieListItem from "./movieListItem.jsx";
import Button from "../filter/button.jsx";

class Movies extends Component {
    render() {
        return (
            <div className="movie-list">
                <ul className="movies">
                    { this.props.movies.map( movie => ( <MovieListItem key={movie.id} movie={movie} /> )) }
                </ul>
                <div className="pagination">
                    <Button onClick={this.props.onPageDecrease}>Previous</Button>
                    <span>{`Page ${this.props.page}`}</span>
                    <Button onClick={this.props.onPageIncrease}>Next</Button>
                </div>
            </div>
        );
    }
}

export default Movies;


