import React, { Component } from "react";
import "./movieListItem.css";
import { Link } from "react-router-dom";

class MovieListItem extends Component {
    constructor(props) {
        super(props)

        this.state = {
            titleIsVisibile: false,
            thumbnailOpacity: 1
        }
        
        this.handleMouseEnter = this.handleMouseEnter.bind(this)
        this.handleMouseLeave = this.handleMouseLeave.bind(this)
    }

    handleMouseEnter() {
        this.setState({ titleIsVisible: true, thumbnailOpacity: 0.15 })
    }

    handleMouseLeave() {
        this.setState({ titleIsVisible: false, thumbnailOpacity: 1 })
    }

    render() {
        const { id, title, poster_path, release_date, vote_average } = this.props.movie;
        const imgUrl = `https://image.tmdb.org/t/p/w342/${poster_path}`
        const year = release_date.substring(0, 4)
        const titleText = this.state.titleIsVisible ? <div><h2>{title}</h2><p>Click for more info</p></div> : null
        const imgStyle = { opacity: this.state.thumbnailOpacity }

        return (
            <li className="movie-item" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave}>
                <Link to={process.env.PUBLIC_URL + '/movies/' + id} className="thumbnail">
                    <div className="movie-image">
                        <img style={imgStyle} src={imgUrl} alt="" />
                        {titleText}
                    </div>
                    <div className="movie-description">
                        <section className="movie-details">
                            <div className="movie-year">
                                <span className="title">Year</span>
                                <span>{year}</span>
                            </div>
                            <div className="movie-rating">
                                <span className="title">Rating</span>
                                <span>{vote_average}</span>
                            </div>
                        </section>
                    </div>
                </Link>
            </li>
        );
    }
}

export default MovieListItem;


