import React, { Component } from "react";
import "./movie.css";

import LoadingMovie from "./loadingMovie.jsx";
import NotFound from "../notFound.jsx";

class Movie extends Component {
    constructor(props) {
        super(props)

        this.state = {
            isLoading: true,
            movie: {}
        }
    }

    componentDidMount() {
        const { movieId } = this.props.match.params
        const movieUrl = `https://api.themoviedb.org/3/movie/${movieId}?api_key=${process.env.REACT_APP_TMDB_API_KEY}&language=en-US`

        fetch(movieUrl)
            .then(res => res.json())
            .then(data => this.setState({movie: data, isLoading: false}))
            .catch(error => console.log("Error,", error))
    }

    render() {
        const { title, backdrop_path, release_date, genres, overview, vote_average, runtime } = this.state.movie
      
        const releaseYear = release_date ? release_date.substring(0, 4) : null
        const imgUrl = `http://image.tmdb.org/t/p/w1280/${backdrop_path}`
        const backgroundStyle = {
        backgroundImage: `url(${imgUrl})`
        }

        let rating = [];

        if(vote_average % 2 === 0) {
            for(let i = 0; i < vote_average; i++) {
                rating.push(<i key={i} className="fas fa-star star"></i>)
            }
        }
        else {
            for(let i = 0; i < vote_average-1; i++) {
                rating.push(<i key={i} className="fas fa-star star"></i>)
            }
            rating.push(<i key={vote_average} className="fas fa-star-half star"></i>)
        }
        
        return (
            <div className="movie-page">
                {
                    this.state.isLoading 
                        ? <LoadingMovie /> 
                        : this.state.movie.status_code 
                            ? <NotFound /> 
                            :   <div>
                                    <div className="movie-image" style={backgroundStyle} />
                                    <div className="movie-details">
                                        <h1>{title} <span>({releaseYear})</span></h1>
                                        <section className="genres">
                                            { genres.map((genre, index) => (
                                                <div key={genre.id}>
                                                    <span>{genre.name}</span>
                                                    { index < genres.length - 1 && (<span className="separator">|</span>)}
                                                </div>
                                            ))}
                                        </section>
                                        <h5>Rating: <span>{rating}</span></h5>
                                        <h5>Runtime: <span>{`${runtime} min`}</span></h5>
                                        <h4>Overview</h4>
                                        <p>{overview}</p>
                                    </div>
                                </div>
                }
            </div>
        );
    }
}

export default Movie;


